<template>
  <span>
      {{numberInfo}}
  </span>
</template>

<script>
export default {
 name:"numberMath",
 props:['number'],
 data(){
     return{
         numberInfo:0
     }
 },
 mounted(){
     let self = this;
     self.numberInfo = self.number+100
 }
}
</script>

