         },
         searchInfo:"双枪,李向阳",
         radio:"",
         testName:"张三",
         testAge:"23",
         input:''
     }
 },

 filters:{
     searchFilter(val){
         if(!val) return '';
         if(val.indexOf(',')>-1){
              return val.replace(/,/g,"")
         }
     }
 },
 methods:{
    
     deepAlive(){
         let self = this;
         let djIndex = self.$root.myKeeps.indexOf("About")
         self.$root.myKeeps.splice(djIndex,1)
     },
